import { useNavigate } from 'react-router'
import { Button } from 'antd-mobile'
import * as images from './images'
import './index.css'

const HomeMobile = () => {
  const navigate = useNavigate()

  return (
    <div className="home-mobile-box">
      <div className="home-mobile-userinfo">
        <img
          className="hmu-avatar"
          src="https://sf6-cdn-tos.douyinstatic.com/obj/eden-cn/ptlz_zlp/ljhwZthlaukjlkulzlp/root-web-sites/avatarDemo.jpeg"
          alt=""
        />
        <div className="hmu-right">
          <div className="hmu-name">张三</div>
          <div className="hmu-study">
            <span>学习时长</span>
            <span>46小时21分钟</span>
          </div>
        </div>
      </div>
      <div className="home-mobile-function">
        <div className="hmf-title">
          <span>功能</span>
        </div>
        <div className="hmf-content">
          <div
            className="hmfc-item"
            onClick={() => {
              navigate('/video')
            }}
          >
            <img src={images.video} alt="" />
            <span>视频学习</span>
          </div>
          <div
            className="hmfc-item"
            onClick={() => {
              navigate('/policy')
            }}
          >
            <img src={images.policy} alt="" />
            <span>政策法规学习</span>
          </div>
          <div
            className="hmfc-item"
            onClick={() => {
              navigate('/scores')
            }}
          >
            <img src={images.achievement} alt="" />
            <span>考试成绩</span>
          </div>
          <div
            className="hmfc-item"
            onClick={() => {
              navigate('/express')
            }}
          >
            <img src={images.express} alt="" />
            <span>证书(快递)</span>
          </div>
          <div
            className="hmfc-item"
            onClick={() => {
              navigate('/user')
            }}
          >
            <img src={images.user} alt="" />
            <span>个人资料</span>
          </div>
          <div
            className="hmfc-item"
            onClick={() => {
              navigate('/modify')
            }}
          >
            <img src={images.password} alt="" />
            <span>修改密码</span>
          </div>
        </div>
      </div>
      <div className="home-mobile-exam">
        <div className="hme-title">
          <span>考试</span>
        </div>
        <div className="hme-content">
          <div className="hmec-mock-exam">
            <img src={images.exam1} alt="" />
            <Button
              className="hmec-exam-ant-btn"
              color="primary"
              size="small"
              onClick={() => {
                navigate('/examtips', {
                  state: { btnName: '模拟考试' },
                  replace: false,
                })
              }}
            >
              模拟考试
            </Button>
            {/* <span
              onClick={() => {
                navigate('/examtips', {
                  state: { btnName: '模拟考试' },
                  replace: false,
                })
              }}
            >
              模拟考试
            </span> */}
          </div>
          <div className="hmec-mock-exam">
            <img src={images.exam2} alt="" />
            <Button
              className="hmec-exam-ant-btn"
              color="primary"
              size="small"
              onClick={() => {
                navigate('/examtips', {
                  state: { btnName: '正式考试' },
                  replace: false,
                })
              }}
            >
              正式考试
            </Button>
            {/* <span
              onClick={() => {
                navigate('/examtips', {
                  state: { btnName: '正式考试' },
                  replace: false,
                })
              }}
            >
              正式考试
            </span> */}
          </div>
        </div>
      </div>
    </div>
  )
}

export default HomeMobile
